@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratregular.woff2") format("woff2"), url("../fonts/montserratregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratmedium.woff2") format("woff2"), url("../fonts/montserratmedium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratsemibold.woff2") format("woff2"), url("../fonts/montserratsemibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratbold.woff2") format("woff2"), url("../fonts/montserratbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1023.5px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767.5px) {
  .container {
    padding: 0 10px;
  }
}

.head-container {
  color: #f9fbfd;
  background-color: #011c40;
  background-image: url("../img/main-back-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023.5px) {
  .head-container {
    background-image: url("../img/main-back-tablet.jpg");
    box-shadow: none;
  }
}

@media (max-width: 767.5px) {
  .head-container.is-closed .main-nav__wrapper {
    display: none;
  }
  .head-container.is-opened .main-nav__wrapper,
.head-container.no-js .main-nav__wrapper {
    display: block;
  }
  .head-container.is-closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 9px;
    left: 3px;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 5px 0 0 #f9fbfd, 0 10px 0 0 #f9fbfd;
  }
  .head-container.is-opened .main-nav__toggle {
    top: 17px;
    right: 7px;
  }
  .head-container.is-opened .main-nav__toggle::before,
.head-container.is-opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 13px;
    left: 6px;
    width: 18px;
    height: 2px;
    z-index: 2;
    background-color: #011c40;
  }
  .head-container.is-opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .head-container.is-opened .main-nav__toggle::after {
    transform: rotate(-45deg);
    box-shadow: none;
  }
  .head-container.is-opened .main-header__logo,
.head-container.no-js .main-header__logo {
    z-index: 12;
    margin-left: 10vw;
  }
  .head-container.is-opened .main-header__logo svg,
.head-container.no-js .main-header__logo svg {
    fill: #011c40;
  }
  .head-container.no-js .main-nav__toggle {
    display: none;
  }
  .head-container.is-opened .intro__wrapper,
.head-container.no-js .intro__wrapper {
    padding-left: 10vw;
  }
}
.main-header {
  position: relative;
}

.main-header__container {
  margin: 0 auto;
  max-width: 1200px;
  width: 85%;
}
@media (max-width: 1023.5px) {
  .main-header__container {
    width: 88.3%;
  }
}
@media (max-width: 767.5px) {
