.main-nav {
  width: 70%;
  max-width: 90%;
}
@media (max-width: 1023.5px) {
  .main-nav {
    width: 90.2%;
  }
}
@media (max-width: 767.5px) {
  .main-nav {
    width: 89%;
  }
}

@media (max-width: 767.5px) {
  .main-nav__wrapper {
    position: absolute;
    padding: 67px 30px 60px 2vw;
    top: 0;
    right: 0;
    z-index: 10;
    width: 89vw;
    min-height: 568px;
    background-color: #f9fbfd;
  }
}

.main-nav__toggle {
  display: none;
}
@media (max-width: 767.5px) {
  .main-nav__toggle {
    position: absolute;
    padding: 0;
    top: 16px;
    right: 12px;
    z-index: 11;
    display: block;
    width: 30px;
    height: 30px;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    cursor: pointer;
  }
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin: 0;
  margin-top: 4px;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023.5px) {
  .main-nav__list {
    margin-top: 2px;
  }
}
@media (max-width: 767.5px) {
  .main-nav__list {
    flex-direction: column;
  }
}

.main-nav__list a {
  padding: 10px 0;
  font-weight: 500;
  line-height: 20px;
  color: #f9fbfd;
  user-select: none;
}
@media (hover: hover) {
  .main-nav__list a:hover:not(.focus-visible) {
    box-shadow: 0 0 2px 4px rgba(70, 130, 180, 0.8);
  }
}
.main-nav__list a.focus-visible:focus {
  box-shadow: 0 0 2px 4px rgba(70, 130, 180, 0.8);
  outline: 2px solid rgba(70, 130, 180, 0.8);
}
@media (max-width: 1023.5px) {
  .main-nav__list a {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 1023.5px) and (hover: hover) {
  .main-nav__list a:hover:not(.focus-visible) {
    box-shadow: none;
  }
  .main-nav__list a:hover:not(.focus-visible).focus-visible:focus {
    box-shadow: none;
    outline: none;
  }
}
@media (max-width: 1023.5px) {
  .main-nav__list a.focus-visible:focus {
    box-shadow: none;
  }
  .main-nav__list a.focus-visible:focus.focus-visible:focus {
    box-shadow: none;
    outline: none;
  }
}
@media (max-width: 767.5px) {
  .main-nav__list a {
    padding: 8px 45px 8px 10px;
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
}

.main-nav__list a:active {
  opacity: 0.3;
}

.main-nav__item {
  margin-left: 4.2%;
}
